@import url(https://enztron-temp-deployed-branch.netlify.app/components.css);
@import url(https://fonts.googleapis.com/css?family=Comfortaa);
:root
{
    --dark-grey     : #2F4858;
    --dark-grey-2   : #282c34;
    --royal-blue    : #29517C;
    --sky-blue      : #0e7490;
    --bluish-white  : #F5F7F9;
    --pure-white    : white;
    --red-color     : #ca0e1d;
    --background-color-dull-yellow: #e0e0d4;
    --yellow-background-color     : #efe6e1;
    --dull-grey     : #8f8f8f;
}

html
{
    font-family: 'Comfortaa', cursive;
}

body
{
    font-family: 'Comfortaa', cursive;
}

html, body
{
    height: auto;
    background-color: white;
}

.App {
    min-height: 100%;
    text-align: center;
    background-color: white;
    font-family: 'Comfortaa', cursive;
}

*::-webkit-scrollbar 
{
    height: 1em;
}
  
*::-webkit-scrollbar-track 
{
    background: var(--light-grey);
}
  
*::-webkit-scrollbar-thumb 
{
    background: #0e7490;
    background: var(--sky-blue);
}

a::after
{
    content: '';
    display: block;
    width: 0px;
    height: 0px;
    background: var(--dark-blue-color);
}

a:hover::after
{
    width: 0;
}
.top-bar
{
    z-index: 15;
    border: none;
    background-color: white;
    padding: 2rem 4rem;
    color: var(--background-color);
    border-bottom: 1px solid var(--background-color);
}

@media only screen and (max-width:1305px){
    .top-bar
    {
        flex-direction: row;
        grid-gap: 1rem;
        gap: 1rem;
    }
}

@media only screen and (max-width:1015px){
    .top-bar
    {
        flex-direction: column;
        grid-gap: 1rem;
        gap: 1rem;
    }
}

.top-bar-brand-name
{
    cursor: pointer;
    margin-right: 3rem;
    color: var(--royal-blue);
}

.search-bar
{
    border: 1px solid var(--royal-blue);
}

.search-bar:active
{
    border: 1px solid var(--royal-blue);
}

.search-bar-input
{
    outline: none;
    font-family: inherit;
}

#search-bar-btn
{
    border: none;
    padding: 9.6px 9.6px 10px 9.6px;
}

.top-bar button
{
    background-color: var(--sky-blue);
}

.solid-primary-btn.navbar-login-btn
{
    background-color: var(--pink-color);
    transition: none;
}

.top-bar button:hover
{
    background: var(--royal-blue)
                radial-gradient(circle, transparent 1%, 
                rgb(54, 99, 146) 1%) center/15000%;
}

.top-bar button:active 
{
    background-color: rgb(54, 99, 146);
    background-size: 100%;
    transition: background 0s;
}
.genre-card
{
    width: 16rem;
    height: 16rem;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
