}

.genre-card:hover 
{
    box-shadow: 0 0 25px 6px var(--offline-grey-color);
    transition: .5s ease-in-out;
    cursor: pointer;
}

.genre-card:not(:hover)
{
    transition: .5s ease-in-out;
}

.genre-card p
