}

.range-input input
{
    width: 100%;
    position: absolute;
    bottom: 1.3rem;
    right: -2px;
    background: none;
    pointer-events: none;
    -webkit-appearance:none;
}

input[type="range"]::-webkit-slider-thumb
{
    height: 1.7rem;
    width:  1.7rem;
    border-radius: 50%;
    -webkit-appearance:none;
    pointer-events: auto;
    background: #0e7490;
}

.product-category
{
    display: flex;
