}
.toasts-container
{
    font-size: 14px;
    position: -webkit-sticky;
    position: sticky;
    bottom: 5px;
    right: 5px;
    z-index : 10;
    overflow: hidden;
}

.bottom-right
{
    position: fixed;
    bottom : 1rem;
    right : 1rem;
    -webkit-animation: toast-from-right 0.7s;
            animation: toast-from-right 0.7s;
}

.notification
{
    margin-bottom : 1rem;
    border-radius : 4px;
    box-shadow : 0 0 10px #999;
    color : #000;
    transition: 0.3s ease;
}

.notification:hover
{
    box-shadow: 0 0 12px #fff;

}

.toast
{
    width : 365px;
    color : #fff;
    padding : 15px 15px 15px 15px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-family: Arial, Helvetica, sans-serif;
    -webkit-animation: toast-from-right 0.7s;
            animation: toast-from-right 0.7s;
}

.toast-content-container
{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.toast-title, .toast-description
{
    margin: 0;
}

.toast-close-btn
{
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    padding: 6.5px 10px;
    border-radius: 50%;
    border: none;
}

/* Toast should come from right */
@-webkit-keyframes toast-from-right
{
    from {
        -webkit-transform: translateX(100%);
                transform: translateX(100%)
    }
    to {
        -webkit-transform: translateX(0%);
                transform: translateX(0%)
    }
}
@keyframes toast-from-right
{
    from {
        -webkit-transform: translateX(100%);
                transform: translateX(100%)
    }
    to {
        -webkit-transform: translateX(0%);
                transform: translateX(0%)
    }
}
