    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

.cart-item-quantity-input
{
    outline: none;
    width: 5rem;
    margin: 0 1rem;
    text-align: center;
}

.cart-horizontal-card-btns
{
    flex-direction: column;
    grid-gap:1rem;
    gap:1rem;
}

.cart-horizontal-card-btns button
{
    margin: 0;
}

.cart-bill
{
    width: 50.9rem;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    grid-column: 3/5;
    padding: 2rem;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    color: black;
    background-color: white;
    border: 1px solid rgb(211, 211, 211);
}

@media only screen and (max-width:1125px)
{

    .cart-bill
    {
        grid-column: 2/4;
        grid-row: 2/4;
    }

    .cart-items-grid
    {
        grid-column: 2/4;
        grid-row: 4/auto;
    }
}
.orders-content-container
{
    display: flex; 
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    padding: 3rem;
}

.no-orders-message-container
{
    margin-top: 2rem;
    min-width: 31rem;
}

.orders-container
{
    display: flex;
    flex-direction: column;
    grid-gap: 2rem;
    gap: 2rem;
}

.orders-container .card-basic-horizontal
{
    cursor: default;
    max-width: 160%;
    width: 60rem;
}

.orders-container .card-basic-horizontal #cart-item-detail
{
    max-width: 34.2rem;
}

.orders-container .card-basic-horizontal:hover
{
    box-shadow: 0 0 1px rgb(48 48 48 / 54%);
}
